<template>
    <div class="head">
        <div class="head-con ">
            <nav class="navbar navbar-expand-lg navbar-light row">
                <a class="navbar-brand" href="#">
                    <img src="../../static/img/logo.png" width="30" height="30" alt="">
                </a>
                <div class=" navbar-collapse justify-content-end" id="navbarSupportedContent">
                    <ul class="navbar-nav">
                        <li class="nav-item main-btn">
                            <p>首页</p>
                            <a class="nav-link main-ym" href="#"></a>
                        </li>
                        <li class="nav-item about-btn">
                            <p>关于</p>
                            <a class="nav-link about-ym" href=""></a>
                        </li>
                        <!-- <li class="nav-item about-btn">
                            <p>关于</p>
                            <a class="nav-link about-ym" href=""></a>
                        </li> -->
                        <li class="nav-item anlie-btn">
                            <p>品牌</p>
                            <a class="nav-link anlie-ym" href=""></a>
                        </li>
                        <li class="nav-item code-btn">
                            <a class="nav-link code-ym" href="javascript:;"></a>
                        </li>
                    </ul>
                </div>
                <div class="head-menu visible-xs">
                    <i></i>
                </div>
            </nav>
        </div>
        <div class="bigCode">
            <img src="../../static/img/bigcode.jpg" alt="">
        </div>
    </div>

</template>

<script>
    export default {
        name:'Head'
    }
    $(function(){
        $(".head-menu").on("click",function() {
            $(".navbar-collapse").toggleClass("visible-xs");
        });
        $(".nav-item").on("mouseover",function(){
            $(this).find("p").css('opacity',1);
        });
        $(".nav-item").on("mouseout",function(){
            $(this).find("p").css('opacity',0);
        });

        $(".code-btn").on("click",function() {
            $(".bigCode").toggleClass("bigCode-move");
        });

        window.onresize = function(){
            console.log(document.body.clientWidth)
            if (document.body.clientWidth < 768) {
              console.log('移动设备');
              window.location.href =  'http://www.ldcywh.com/ledaom/index.html'
            }
        }
    })
</script>

<style lang="css">
.bg-light{background-color: #fff !important;}
.head-left{text-align: left;}
.head-con{font-size: 16px;position: fixed;top: 0;z-index: 100;background-color: #62c4d1;width: 100%;padding: 0 34px;}
.navbar-light .navbar-nav .nav-link{color: #000;width: 48px;height: 24px;padding: 0 ; margin: .5rem;}
.head li{padding:0 20px;position: relative;}
.navbar-brand img{width: 129px;height: 24px;}
.nav-item p{position: absolute;z-index: 0;text-align: center;left: 0px;opacity: 0;width: 100%;line-height: 40px;background-color: #62c4d1;margin: 0;}
.main-ym{background: url(../../static/img/main-icon.png) no-repeat;background-size: auto 100%;background-position: center;}
.anlie-ym{background: url(../../static/img/case-icon.png) no-repeat;background-size: auto 100%;background-position: center;}
.about-ym{background: url(../../static/img/about-icon.png) no-repeat;background-size: auto 100%;background-position: center;}
.code-ym{background: url(../../static/img/anlie-icon.png) no-repeat;background-size: auto 100%;background-position: center;}
.head a:hover{color: #fff !important;}
.head-menu{display: none;position: absolute;right: 4%;top:16px;width: 40px;height: 26px;background: url(../../static/img/menu.png) no-repeat;background-size: 100% 100%;}
@media (max-width: 767px){.visible-xs {display: block !important;}
.navbar-collapse{display: none;}}
@media (min-width: 767px){.navbar-collapse{display: block;}}
.bigCode{position: absolute;
    width: 100%;
    top: 56px;
    padding: 60px;
    background-color: #165a63;z-index: 10;}
    .bigCode img{
        width: 157px;
    }

.bigCode {
    -webkit-transform: perspective(600px) rotateX(-90deg);
    -moz-transform: perspective(600px) rotateX(-90deg);
    -ms-transform: perspective(600px) rotateX(-90deg);
    transform: perspective(600px) rotateX(-90deg);
    -webkit-transform-origin: center top 0;
    -moz-transform-origin: center top 0;
    -ms-transform-origin: center top 0;
    transform-origin: center top 0;
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    -ms-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-transition: all .3s ease .2s;
    -moz-transition: all .3s ease .2s;
    -ms-transition: all .3s ease .2s;
    transition: all .3s ease .2s;
    text-align: center;
}
/*下拉框打开*/
.bigCode-move {
    transform: perspective(0px) rotateX(0deg);
}
</style>



