<script>
import bottomBar from './components/bottomBar'
import canvasBg from './components/canvasBg'
import Head from './components/Head'
export default {
    name: 'About',
    components: {
        bottomBar,
        canvasBg,
        Head
    },
    data:function(){
        return{
            aboutTop:'',
            aboutBimg1:'',
            aboutBimg2:'',
            aboutBimg3:'',
            aboutBimg4:'',
            aboutBimg5:'',
            aboutBimg6:'',
            aboutBimg7:'',
            aboutBaner:'',
            serviceLists:'',
            serviceList1:'',
            serviceList2:'',
            serviceList3:'',
            serviceList4:'',
            serviceimg1:'',
            serviceimg2:'',
            serviceimg3:''

        }
    },
    created(){
        this.aboutF(),
        this.serviceF()
    },
    methods:{
        aboutF:function(){
            this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/about')
            .then((res)=>{

                this.aboutBaner=res.data.data.banner[0].bannerUrl;
                // console.log(res.data)
                this.aboutTop=res.data.data.about_dtl;
                this.aboutBimg1=res.data.data.about_pic[0];
                this.aboutBimg2=res.data.data.about_pic[1];
                this.aboutBimg3=res.data.data.about_pic[2];
                this.aboutBimg4=res.data.data.about_pic[3];
                this.aboutBimg5=res.data.data.about_pic[4];
                this.aboutBimg6=res.data.data.about_pic[5];
                this.aboutBimg7=res.data.data.about_pic[6];
                // console.log(this.aboutBimg6)
                setTimeout(function(){
                    var top = $(".about-con5").offset().top;
                    console.log(top);
                    $(".wrapslide1").css("top",top)
                },500)

            })
        },
        serviceF:function(){
            this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/service')
            .then( (res) => {
                 this.serviceLists=res.data.data.service_list;

                 this.serviceimg1=res.data.data.banner[0].bannerUrl;
                 // this.serviceimg2=res.data.data.banner[1].bannerUrl;
                 // this.serviceimg3=res.data.data.banner[2].bannerUrl;
                 this.serviceList1=res.data.data.service_list[0];
                 this.serviceList2=res.data.data.service_list[1];
                 this.serviceList3=res.data.data.service_list[2];
                 this.serviceList4=res.data.data.service_list[3];
                 // console.log(res.data)
            })
        }
    }

}

$(function(){
    var shadowTop = function() {
        $(".con-shadow-before").mouseover(function() {
            $(this).addClass("con-shadow-after")
        });
        $(".con-shadow-before").mouseout(function() {
            $(this).removeClass("con-shadow-after")
        })
        $(".main-btn").on("click",function(){
            window.location.href ='./index.html'
        });
        $(".service-btn").on("click",function(){
            window.location.href ='./service.html'
        });
        $(".about-btn").on("click",function(){
            window.location.href ='./about.html'
        });
        $(".anlie-btn").on("click",function(){
            window.location.href ='./case.html'
        });
    };
    shadowTop();
})
