-o-animation: danyuan_men_point .2s linear alternate forwards;
animation: danyuan_men_point .2s linear alternate forwards;}
@-webkit-keyframes danyuan_men_point {0% { transform:translateY(0px);}100%{ transform:translateY(-5px);}}
@-o-keyframes danyuan_men_point {0% { transform:translateY(0px);}100%{ transform:translateY(-5px);}}
