<template>
    <div class="bottomBar">
        <div class="bottomBar-top">
            <div class="title">
                <div class="bottomBar-top-con">
                    <p class="bottomBar-line bottomBar-line1">
                        <span></span>
                    </p>
                    <p>GET IN TOUCH</p>
                    <p class="bottomBar-line bottomBar-line2">
                        <span></span>
                    </p>
                </div>
                <p>
                    <span>联系方式</span>
                </p>
            </div>
            <div class="container">
                <div class="row bottomBar-top-left">
                    <li class="col-md-3">
                        <a class="bottomBar-tel" href="javascript:;"></a>
                        <p>0755-83226965</p>
                    </li>
                    <li class="col-md-3">
                        <a class="bottomBar-email" href="javascript:;"></a>
                        <p>3433161083@qq.com</p>
                    </li>
                    <li class="col-md-3">
                        <a class="bottomBar-qq" href="javascript:;"></a>
                        <p>3433161083</p>
                    </li>
                    <li class="col-md-3 border-none">
                        <a class="bottomBar-address" href="javascript:;"></a>
                        <p>深圳市南山区华侨城文化创意园北区A3栋501</p>
                    </li>
                </div>
            </div>
        </div>
        <div class="bottomBar-bootom">
            <p>Copyright ©深圳乐道广告有限公司 技术支持：形色视觉：粤ICP备18029969号</p>
        </div>
    </div>
</template>

<script>
export default {
    name:'bottomBar',
    data:function(){
        return{
            ma:''
        }
    },
    created(){
        this.bottomF()
    },
    methods:{
        bottomF:function(){
            this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/about')
            .then((res)=>{
                // console.log(res.data)
                this.ma = res.data.data.contact.qrcode;
            })
        }
    }
}

</script>

<style lang="css">
.title{
    padding-top: 51px;
}
.bottomBar{}
.bottomBar-top{background-color: #62c4d1;padding-bottom: 30px;}
.bottomBar-top-con p{position: relative;display: inline-block;}
.bottomBar-line{width: 122px;height: 1px;background-color: #46787e;line-height: 6px;}
.bottomBar-line span{padding: 0 !important;margin: 0;position: absolute;width: 5px;height: 5px;background-color: #3f3a39;border-radius: 50%;top: -1.5px;}
.bottomBar-line1{margin-right: 8px;}
.bottomBar-line1 span{right: 0;}
.bottomBar-line2 span{left: 0;}
.bottomBar-bootom p{height: 80px;line-height: 80px;margin: 0;font-size: 13px;color: #000;text-align: center;}
.bottomBar-address{vertical-align: middle;display: inline-block;width: 40px;height: 40px;margin-right: 10px;margin-bottom: 3px;background: url(../../static/img/address.png) no-repeat;background-size: contain;}
.bottomBar-qq{vertical-align: middle;display: inline-block;width: 40px;height: 40px;margin-right: 10px;margin-bottom: 3px;background: url(../../static/img/qq.png) no-repeat;background-size: contain;}
.bottomBar-tel{vertical-align: middle;display: inline-block;width: 40px;height: 40px;margin-right: 10px;margin-bottom: 3px;background: url(../../static/img/tel.png) no-repeat;background-size: contain;}
.bottomBar-email{vertical-align: middle;display: inline-block;width: 40px;height: 40px;margin-right: 10px;margin-bottom: 3px;background: url(../../static/img/mail.png) no-repeat;background-size: contain;}
.bottomBar-top-left li{text-align: left;line-height: 30px;font-size: 16px;border-right: 1px solid #4a838a;padding: 0 30px;}
.bottomBar-top-right{text-align: right;}
.bottomBar-top-right img{width: 247px;height: 247px;}
.bottomBar-top .container{padding-bottom: 20px;}

.title{height: 165px;}
.title h3{padding-top: 50px;color: #3f3a39;font-weight: 600;text-align: center;font-size: 1.3rem;letter-spacing: 2px;}
.title p{margin-top: 0;
    margin-bottom: .5rem;
    font-size: 1.3rem;
    color: #3f3a39;
    text-align: center;
    letter-spacing: 8px;
    font-weight: 600;}
.title span{line-height: 38px;display: inline-block;text-align: center;padding-left: 13px;}
.bottomBar-top .title span{color: #3f3a39;
    font-size: 21px;
    font-weight: 500;}
    .border-none{border: none !important;}
    .bottomBar-top-left li p{
        margin: 0;
    }
</style>



